.services-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
    margin-bottom: 1rem;
}
.first-service {
    background-color: #363333 !important;
    color: white;
    border: 1px solid yellow;
    margin-top: 0;
  }
  
  .first-service .services-content-container {
    background-color: transparent !important; 
  }
  
  .first-service .services-content {
    background-color: transparent !important; 
  }
  
  .first-service h2,
  .first-service p {
    background-color: transparent !important; 
    color: white;
    
  }
