.first-service h2, .second-service h2, .third-service h2, .fourth-service h2{
    font-size: 2.3rem;
    font-weight: bolder;
}
.first-service p, .second-service p, .third-service p, .fourth-service p{
    font-size: 1.5rem;
    font-weight: 400;
}
.services-content-container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;

    
}
.services-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 60%;
}
.services-img{
    width: 15rem;
    border-top-left-radius: 5rem;
    border-top-right-radius: 7rem;
    border-bottom-left-radius: 2rem;
    border-bottom-right-radius: 4rem;
}
.second-service , .second-service h2, .second-service p{
    background-color: #272121;
}
.black{
    color: #272121;
    font-weight: bold;
    background: none;
}

.services-main-title{
    margin-top: 1rem;
    margin-bottom: -2rem;
    z-index: 2;
}
@media (max-width:1000px) {
    .first-service h2, .second-service h2, .third-service h2, .fourth-service h2{
      font-size: 2rem;
      text-align: center;
      width: 20rem;
  }
  .first-service p, .second-service p, .third-service p, .fourth-service p{
      font-size: 1.1rem;
  }
  .services-img{
    width: 10rem;
  }
}

