@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100..700;1,100..700&display=swap');


.about-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.about-container h2{
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;

}
.orange{
    color: orange;
    font-weight: bold;
    background: none;
}
.about-content{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}
.about-content p{
    width: 60%;
    font-size: 1.5rem;
    font-weight: 500;
    
}
@media (max-width:820px) {
    .about-content p{
        font-size: 1rem;
    }
    .about-content img{
        width: 10rem;
    }
}
@media (max-width:511px) {
    .about-content p{
        font-size: .9rem;
    }
    .about-content img{
        width: 8rem;
    }
    .about-container h2{
        font-size: 1.2rem;
    }   
}
@media (max-width:393px) {
    .about-content p{
        font-size: .7rem;
    }
    .about-content img{
        width: 7rem;
    }
}
@media (max-width:317px) {
    .about-content p{
        font-size: .6rem;
    }
    .about-content img{
        width: 6rem;
    }
}